<template>
    <div>
        <div @click="setMutation">啦啦啦啦啦</div>
        <div @click="setActions">滴滴滴滴滴</div>
    </div>
</template>

<script>
import { mapMutations, mapGetters, mapActions } from "vuex";

export default {
    created() {
        console.log(this.someData);
    },
    computed: {
        ...mapGetters([
            "someData"
        ])
    },
    methods: {
        ...mapMutations({
            setSomeData: "SET_SOMEDATA"
        }),
        ...mapActions([
            "selectSet"
        ]),
        setMutation() {
            const obj = {
                x: "xxx",
                y: "yyy"
            };
            this.setSomeData(obj);
        },
        setActions() {
            const obj = {
                x: "act",
                y: "ions"
            };
            this.selectSet(obj);
        }
    }
};
</script>

<style>

</style>



