<template>
    <div class="child">
        this is polymerizationChild
        <button @click="changeParams">参数变化</button>
    </div>
</template>

<script>
export default {
    data() {
        return {
        }
    },
    methods: {
        changeParams() {
            this.$router.push({name: 'polyChild', params: {id: 1562}});
            
        }
    },
    beforeRouteEnter(to, from, next) {
        //does NOT have access to `this` component instance}
        console.log(to, "entry");
        next();
    },
    beforeRouteUpdate(to, from, next) {
        console.log(to, "update");
        if (to.params.id == 1562) {
            console.log("路由参数变了");
            console.log(this);
        }
        next();
    }
}
</script>

<style lang="scss" scoped>
    
    .child {
        padding-bottom: 50px;
        border-bottom: 1px solid black;
    }
</style>



